import React from 'react';
import { Container, Title, ButtonGroup, Button } from './style';

const Menu = ({range, setRange}) => {

    return (
        <Container>
            <Title>Selecione o perodo de tempo que voc deseja visualizar seus dados:</Title> 
            <ButtonGroup>
                <Button isSelected={range==='today'} onClick={() => setRange('today')}>Hoje</Button>
                <Button isSelected={range==='week'} onClick={() => setRange('week')}>H uma semana</Button>
                <Button isSelected={range==='month'} onClick={() => setRange('month')}>H um ms</Button>
                <Button isSelected={range==='all'} onClick={() => setRange('all')}>Todo o perodo</Button>
            </ButtonGroup>
        </Container>
    )
}

export default Menu;